  var swiper1 = new Swiper('.slider-popular-destinations .swiper-container', {
    // Optional parameters
    // direction: 'vertical',
    loop: false,
    slidesPerView: 4,
    spaceBetween: 30,
    // Navigation arrows
    navigation: {
      nextEl: '.slider-popular-destinations .swiper-button-next',
      prevEl: '.slider-popular-destinations .swiper-button-prev',
    },
    breakpoints: {
      0: {
        slidesPerView: 1.5,
        spaceBetween: 10
      },
      640: {
        slidesPerView: 2.2,
        spaceBetween: 10,
        navigation: ''
      },
      768: {
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: ''
      },
      1024: {
        slidesPerView: 4,
        spaceBetween: 30
      },
    },
    // And if we need scrollbar
    scrollbar: {
      el: '.slider-popular-destinations .swiper-scrollbar',
    },
  });

  // Slide Testimonial
  function updSwiperNumericPagination() {
    this.el.querySelector( '.swiper-counter-testimonial' )
    .innerHTML = '<span class="count absolute font-medium top-0 left-0">0'+ (this.realIndex + 1) +'</span><span class="total absolute font-medium top-0 right-0">0'+ this.el.slidesQuantity +'</span>';
  }

  document.addEventListener( 'DOMContentLoaded', function () {
    document.querySelectorAll( '.slider-testimonial .swiper-container' ).forEach( function( node ) {
      node.slidesQuantity = node.querySelectorAll( '.swiper-slide' ).length;

      new Swiper(node, {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: false,
        // cssWidthAndHeight: true,
        centeredSlides: false,
        pagination: {
          el: '.slider-testimonial .swiper-pagination',
          type: 'progressbar',
        },
        on: {
          init: updSwiperNumericPagination,
          slideChange: updSwiperNumericPagination
        },
      })
    })
  })

  // Slide Recommended
